//     writer: 'Liu Cixin',
//     price: 40,
//     isbn: '278-3-16-148410-0',
//     inventory: 7
//   }
// ]

export default {
  name: 'table',
  data () {
    return {
      loading: true,
      tableData: [],
      currentPage: 1,
      totalPage: 0,
      selectedRows: [],
      filters: {
        name: {value: '', keys: ['name']}
      }
    }
  },
  methods: {
    handleBookClick () {
      var data = 'books'
      http
        .post('/books', data)
        .then(response => {
          this.tableData = response.data
          console.log(response.data)
        }).finally(() => this.loading = false)
        .catch(e => {
          console.log(e)
        })

      this.submitted = true
    }
  },
  mounted () {
    axios
      .get('relative/books')
      .then(response => (
        this.tableData = response.data))
    console.log(this.tableData)
  }
}
</script>

